import React, { Component } from 'react';
import CardList from '../components/CardList';
import SearchBox from '../components/SearchBox';
import Scroll from '../components/Scroll';
import ErrorBoundary from '../components/ErrorBoundary';
import './App.css';

class App extends Component {
	constructor() {
		super();
		this.state = {
			robots: [],
			searchfield: ''
		}
	}

	componentDidMount() {
		fetch('https://jsonplaceholder.typicode.com/users')
		.then(response => response.json())
		.then(users => this.setState({robots: users})); // Getting data in the form of JSON from the API
	}

	onSearchChange = (event) => {     //methods are declared as normal functions in C++, Java
		// console.log(event.target.value);
		this.setState({ searchfield: event.target.value });
	}

	render() {
    const { robots, searchfield } = this.state; //destructures this.state.robots and searchfield
		const filteredRobots = robots.filter(robot => {
			return robot.name.toLowerCase().includes(searchfield.toLowerCase());
		})

		return (!robots.length)?  //Returns false
			<h1 className='tc'>Loading</h1>
		 :                       
			(
				<div className='tc'>
					<h1 className='f1'>RoboFriends</h1>
					<SearchBox searchChange={this.onSearchChange}/>
          <Scroll>
            <ErrorBoundary>
              <CardList robots={filteredRobots}/>
            </ErrorBoundary>
          </Scroll>
				</div>
			);
		}	
}

export default App;